@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Rajdhani','Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #161B15;
}
.Home {
    background-color: #161B15;
    color: antiquewhite;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.movies-list-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 90%;
    height: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.movies-list-item { 
    display: -webkit-flex; 
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 15px;
    max-width: 200px;
    word-wrap: break-word;
}

.movies-list-item:hover{cursor: pointer; opacity: 0.7;}

.movie-image {
    max-height: 200px;
    max-width: 200px;
}

.search-bar {
    width: 400px;
    font-size: 1.3rem;
    text-align: center;
    margin-top: 20px;
    border: none;
    background-color: inherit;
    border-bottom: 3px solid black;
    color: antiquewhite;
}

.btn {
    background-color: inherit;
    color: antiquewhite;
    cursor: pointer;
    height: 30px;
    width: auto;
}

.nav-btn-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

h3 {
    text-align: center;
}
.movie-page-container 
{
    background-color: #161B15;
    color: antiquewhite;
} 

.movie-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 25px;
}

.fav-btn{
    max-width: 100px;
}
